import React from "react";
import './Navbar.css';
import { useState, useEffect } from "react";
import { Link } from "react-scroll";
import styled from 'styled-components'


function Navbar() {

  const [NavbarSticky, setNavbarSticky] = useState(false);

  const handleSticky = () => {
    if (window.scrollY >= 550) {
      setNavbarSticky(true)
    }
    else{
      setNavbarSticky(false)
    }
  }

  useEffect(() => {
    window.addEventListener('scroll',
    handleSticky)
    return () => {
      window.removeEventListener('scroll',
      handleSticky)
    }
  }, [])


  return (
      <div class={NavbarSticky ? 'navbar fixed-top navbar-expand-lg navbar-dark bg-dark p-md-3': 'navbar fixed-top navbar-expand-lg navbar-dark bg-dark p-md-3'}>
      <div class="container">
      <Link class="navbar-brand text-white" href="/"><strong style={{fontSize:"30px",lineHeight:"35px"}}>PHLPAT</strong></Link>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
        style={{backgroundColor:"black"}}
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarNav">
        <div class="mx-auto"></div>
        <ul class="navbar-nav active">
          <div style={{marginTop:"10px"}}></div>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <Link className="NavLink" activeClass="is-active" spy={true} smooth={true} duration={300} to="home"><strong>HOME</strong></Link>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <Link className="NavLink" activeClass="is-active" spy={true} smooth={true} duration={300} to="resume"><strong>RESUME</strong></Link>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <Link className="NavLink" activeClass="is-active" spy={true} smooth={true} duration={300} to="portfolio"><strong>PORTFOLIO</strong></Link>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <Link className="NavLink" activeClass="is-active" spy={true} smooth={true} duration={300} to="contact"><strong>CONTACT</strong></Link>
          </li>
        </ul>
      </div>
    </div>
    </div>


  );
}

export default Navbar;